<template>
  <div>
    <!-- 订单信息表格 -->
    <div class="waiting_order_cont">
      <div class="waiting_order" v-for="(order, idx) in tableData" :key="idx">
        <!-- 订单头部 -->
        <div class="order_hd">
          <!-- 头部上侧 -->
          <div class="order_name">
            <div class="shop_info">
              <el-row>
                <el-col :span="4">{{ order.deviceName }}</el-col>
                <el-col :span="4">
                  订单金额：<b>{{ getOrderAmount(order) }} 元</b>
                </el-col>
                <el-col :span="4" v-if="order.orderType == 0"> {{ dateFormat(order.createDatetime) }} 下单 </el-col>
                <el-col :span="4" v-if="order.orderType == 1"> 退款时间：{{ dateFormat(order.createDatetime) }} </el-col>
                <el-col :span="4" v-if="order.orderNo && order.orderType == 0"> 订单编号：{{ order.orderNo }} </el-col>
                <el-col :span="4" v-if="order.orderNo && order.orderType == 1"> 退款订单编号：{{ order.orderNo }} </el-col>
              </el-row>
              <div class="btns">
                <el-button v-if="order.orderType == 0" @click.stop="refuseRefundDialog(order)">退款</el-button>
              </div>
            </div>
            <!--交易状态：0、未付款，1、已付款(待接单)，2、已接单(待发货)，3、已发货(配送中，客户待收货)，4、交易成功(o2o已送达，客户已收货)，15、交易关闭(用户关闭)，16、交易关闭(拒单)，17、交易关闭(未接单)-->
            <!--<div class="time">-->
            <!--<b v-if="order.orderStatus === 0">已取消</b>-->
            <!--<b v-if="order.orderStatus === 1">待支付</b>-->
            <!--<b v-if="order.orderStatus === 2">已支付</b>-->
            <!--<b v-if="order.refundStatus === 0">退款申请</b>-->
            <!--<b v-if="order.refundStatus === 1">退款拒绝</b>-->
            <!--<b v-if="order.refundStatus === 2">退款中</b>-->
            <!--<b v-if="order.refundStatus === 3">已退款</b>-->
            <!--</div>-->
          </div>
          <!-- 头部下侧 -->
          <div class="handle_info" v-if="order.orderType == 0">
            <!-- <div class="price">
              订单金额：<b>{{ getOrderAmount(order) }} 元</b>
            </div> -->
            <div v-if="order.orderType == 1" class="price">
              退款备注：<b style="color: #f00; font-weight: bold;">{{ order.refundMemo }}</b>
            </div>

            <!-- <div class="btns">
              <el-button v-if="order.orderType == 0" @click.stop="refuseRefundDialog(order)">退款</el-button>
            </div> -->
          </div>
          <!-- T-处理中，S-成功，E-失败，R-冲正，C-撤销，F-退款，U-未扫码(二码合一时判断用)，N-未知 -->
          <div v-if="order.orderState == 'S'" class="paid" style="background: #40DE40;">成功</div>
          <div v-else-if="order.orderState == 'T'" class="paid" style="background: #f60;">处理中</div>
          <div v-else-if="order.orderState == 'E'" class="paid" style="background: #f00;">失败</div>
          <div v-else-if="order.orderState == 'R'" class="paid" style="background: #f60;">冲正</div>
          <div v-else-if="order.orderState == 'C'" class="paid" style="background: #f60;">撤销</div>
          <div v-else-if="order.orderState == 'F'" class="paid" style="background: #f60;">退款</div>
          <div v-else-if="order.orderState == 'U'" class="paid" style="background: #f60;">未扫码</div>
          <div v-else-if="order.orderState == 'N'" class="paid" style="background: #f60;">未知</div>
          <!-- <div class="paid" :style="{background:order.isPaid ? '#f60' : '#f00'}">{{order.isPaid ? '已付' : '未付'}}</div> -->
        </div>
        <!-- 订单主体 -->
        <div class="order_bd">
          <!-- 买家信息 -->
          <!-- 订单信息表格 -->
          <el-table :data="[...order]" style="width: 100%; margin-top: 20px;">
            <el-table-column v-if="order.orderType == 0" prop="itemName" label="商品名称" :width="isIE ? '450' : ''"> </el-table-column>
            <el-table-column v-if="order.orderType == 0" prop="amount" label="原价" width="200"> </el-table-column>
            <el-table-column v-if="order.orderType == 1" prop="refundOrderNo" label="原订单编号"> </el-table-column>
            <el-table-column v-if="order.orderType == 1" prop="sellAmount" label="原订单金额" width="200"> </el-table-column>
            <el-table-column v-if="order.orderType == 1" prop="refundSellAmount" label="退款金额" width="200"> </el-table-column>

            <el-table-column prop="drinkType" label="温度" width="200">
              <template slot-scope="scope">
                {{ scope.row.drinkType == 1 ? '热饮' : '冷饮' }}
              </template>
            </el-table-column>
            <el-table-column prop="sugarWeight" label="甜度" width="200">
              <template slot-scope="scope">
                {{ scope.row.sugarWeight == 0 ? '无糖' : scope.row.sugarWeight == 1 ? '半糖' : '全糖' }}
              </template>
            </el-table-column>
            <el-table-column v-if="order.orderType == 0" prop="channelType" label="支付方式" width="200">
              <template slot-scope="scope">
                {{ scope.row.channelType == 'WX' ? '微信' : scope.row.channelType == 'ZFB' ? '支付宝' : '其他' }}
              </template>
            </el-table-column>
            <el-table-column v-if="order.orderType == 1" prop="channelType" label="退款方式" width="200">
              <template slot-scope="scope">
                {{ scope.row.channelType == 'WX' ? '微信' : scope.row.channelType == 'ZFB' ? '支付宝' : '其他' }}
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- 订单尾部 -->
        <!-- <div class="order_ft">
          <div style="text-align: left;"> -->
        <!--<div class="remark">买家留言：{{ order.remark }}</div>-->
        <!-- <div v-if="order.orderType == 1" class="remark">
              退款备注：<b style="color: #f00; font-weight: bold;">{{ order.refundMemo }}</b>
            </div>
          </div>
          <div> -->
        <!-- <div v-if="order.orderType == 0">{{ dateFormat('', '', order.createDatetime, '') }} 下单</div> -->
        <!-- <div v-if="order.orderType == 0">{{ dateFormat(order.createDatetime) }} 下单</div> -->
        <!-- <div v-if="order.orderType == 1">退款时间：{{ dateFormat('', '', order.createDatetime, '') }}</div> -->
        <!-- <div v-if="order.orderType == 1">退款时间：{{ dateFormat(order.createDatetime) }}</div>
            <div v-if="order.orderNo && order.orderType == 0">订单编号：{{ order.orderNo }}</div>
            <div v-if="order.orderNo && order.orderType == 1">退款订单编号：{{ order.orderNo }}</div> -->
        <!--<div v-if="order.trackingNumber">运单号：{{ order.trackingNumber }}</div>-->
        <!-- </div>
        </div> -->
      </div>
    </div>

    <!--退款弹框 -->
    <el-dialog title="退款备注" :visible.sync="refundDialog" width="25%">
      <el-divider></el-divider>
      退款备注：<el-input type="text" v-model="refundMemo" placeholder=""></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="refundDialog = false">取 消</el-button>
        <el-button type="primary" @click="refuseRefund">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import { param } from '../../utils'
import refundPart from './refundPart'

export default {
  components: {
    refundPart
  },
  props: ['orderList'],
  data() {
    return {
      isRefund: false,
      printOrder: {
        details: []
      },
      tableData: [],
      deliveryType: '',
      orderStatus: '',
      orderInfo: '',
      refundDialog: false,
      isIE: false,
      selectId: '',
      refundMemo: ''
    }
  },
  filters: {
    formatPhone: phone => {
      phone = phone + ''
      return phone.slice(0, 3) + '****' + phone.slice(7)
    }
  },
  computed: {
    dateFormat() {
      const that = this
      return data => {
        return this.$moment(data).format('YYYY-MM-DD HH:MM:SS')
      }
    }
  },
  methods: {
    // 查询订单金额
    getOrderAmount(order) {
      if (!order.sellAmount) return 0
      // debugger
      return order.sellAmount
    },
    refuseRefundDialog(order) {
      this.selectId = order.id
      this.refundMemo = ''
      this.refundDialog = true
    },
    // 退款
    refuseRefund() {
      const params = {
        orderId: this.selectId,
        refundMemo: this.refundMemo
      }
      this.$confirm('是否确认退款？', '退款提醒', { type: 'warning' })
        .then(() => {
          this.httpPost('/order/refund', params).then(ret => {
            this.$message.success('退款成功')
            this.$emit('getSearch', '')
          })
        })
        .catch(() => {})
    },
    initLoadData() {
      this.$emit('initLoadData')
    },
    minus(originalPrice, sellPrice) {
      return this.$accSub(originalPrice, sellPrice)
    }
  },

  watch: {
    orderList(n, o) {
      this.tableData = n
    }
  },

  mounted() {
    if (this.orderList.orderType == 1) {
      this.isRefund = true
    }
    var userAgent = navigator.userAgent
    if (userAgent.indexOf('Trident') > -1) {
      //判断是否IE浏览器
      this.isIE = true
    } else {
      this.isIE = false
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 220px !important;
  }
}
// 待接单
.no_order {
  text-align: center;
  line-height: 12;
  font-size: 24px;
}

.waiting_order {
  width: 100%;
  font-size: 14px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  overflow: hidden;
  $orange: #f60;
  margin-bottom: 20px;
  .order_hd {
    padding: 12px 60px 12px 14px;
    border-bottom: #eee solid 1px;
    background-color: #fffbf1;
    position: relative;
    .order_name {
      margin-bottom: 6px;
      display: flex;
      justify-content: space-between;
      .shop_info {
        width: 100%;
        display: flex;
        align-items: center;
        .el-row {
          width: 100%;
        }
      }
      .shop_id {
        display: inline-block;
        padding: 0px 6px;
        height: 32px;
        line-height: 32px;
        color: #fff;
        margin-right: 10px;
        background-color: $color;
        border-radius: 3px;
        font-size: 20px;
      }
      .shop_name {
        font-size: 16px;
      }
    }
    .handle_info {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .paid {
      width: 80px;
      height: 80px;
      background-color: $orange;
      line-height: 136px;
      text-align: center;
      color: #fff;
      position: absolute;
      right: -40px;
      top: -40px;
      transform: rotate(45deg);
    }
    b {
      color: $orange;
      padding: 3px;
    }
  }
  .order_bd {
    padding: 10px;
    .buyer_info {
      margin-bottom: 10px;
      .info_row {
        line-height: 26px;
        border-right: 1px solid #ccc;
      }
      .iconfont {
        margin-right: 10px;
        color: #ccc;
      }
      b {
        color: $orange;
      }
      .address {
        font-size: 12px;
        margin-left: 25px;
      }
      .contact {
        padding-left: 20px;
        line-height: 26px;
        .new_user {
          display: inline-block;
          padding: 4px;
          margin-left: 4px;
          color: #60cd5b;
          line-height: 1;
          border: #38c531 solid 1px;
          border-radius: 4px;
        }
      }
    }
  }
  // 表格
  /deep/.el-table {
    margin-top: 0 !important;
    .el-table__row td {
      padding: 8px 0;
    }
    // 配送费
    .delivery_cost,
    .discount_price {
      padding: 8px 10px;
      display: flex;
      border-bottom: 1px solid #eee;
      i {
        flex: 1;
      }
      span {
        width: 160px;
      }
    }
  }
  .order_ft {
    padding: 10px;
    text-align: right;
    background-color: #f8f8f8;
    display: flex;
    justify-content: space-between;
    div {
      line-height: 24px;
    }
  }
}
.ie_width {
  width: 720px !important;
}
.notie_width {
  width: 420px !important;
}
</style>
