<template>
  <!-- 选择商品对话框 -->
  <el-dialog class="select_shop" title="部分退款" :visible.sync="dialogStatus" width="80%">
    <el-divider></el-divider>
    <el-table :data="details" style="width: 100%; margin-top: 20px">
      <el-table-column prop="chainCode" label="商品编码" width="100"> </el-table-column>
      <el-table-column prop="title" label="商品名称"> </el-table-column>
      <el-table-column prop="quantity" label="数量" width="80"> </el-table-column>
      <el-table-column prop="amount" label="总价" width="100"> </el-table-column>
      <el-table-column prop="refundQuantity" label="退货数量" width="150">
        <template slot-scope="scope">
          <el-input-number controls-position="right" style="width: 100px"  :step-strictly="true" size="mini" v-if="scope.row.chainCode!='退款总计'" v-model="scope.row.refundQuantity" @change="changeRfAmount(scope.row)" :step="1" :min="0" :max="scope.row.quantity"></el-input-number>
        <span v-else>{{scope.row.refundQuantity}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="refundAmount" label="退货金额" width="100">
      </el-table-column>
      <el-table-column prop="marketSpecialId" label="优惠信息" width="200">
        <template slot-scope="scope" v-if="scope.row.marketSpecialId">
          {{ scope.row.originalPrice }}￥
        </template>
      </el-table-column>
    </el-table>

    <span class="select_store_btn">
      <el-button @click.stop="dialogStatus = false">取 消</el-button>
      <el-button type="primary" @click.stop="refundPart">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      dialogStatus: false,
      details: [],
      orderId: ''
    }
  },
  mounted() {},
  methods: {
    showDialog(details, orderId, ifCoupon) {
      this.orderId = orderId
      this.dialogStatus = true
      this.details = details
      // 含有优惠券的话，减去3行，不含优惠券，减去2行
      const subLen = ifCoupon ? 3 : 2
      this.details.splice(this.details.length - subLen, subLen)
      for (let i = 0; i < this.details.length; i++) {
        this.details[i].refundQuantity = 0
        this.details[i].refundAmount = 0
        this.details[i].detailId = this.details[i].id
      }
      const refundAmountAll = {
        chainCode: '退款总计',
        refundQuantity: 0,
        refundAmount: 0
      }
      this.details.push(refundAmountAll)
      console.log(this.details)
    },
    changeRfAmount(row) {
      this.details[this.details.length - 1].refundQuantity = 0
      this.details[this.details.length - 1].refundAmount = this.$accSub(this.details[this.details.length - 1].refundAmount, row.refundAmount)
      row.refundAmount = this.$accMul(row.refundQuantity, row.sellPrice)
      this.details[this.details.length - 1].refundAmount = this.$accAdd(this.details[this.details.length - 1].refundAmount, row.refundAmount)
      for (let i = 0; i < this.details.length - 1; i++) {
        this.details[this.details.length - 1].refundQuantity += this.details[i].refundQuantity
      }
    },
    refundPart() {
      console.log(this.details)
      this.details.pop()
      console.log(this.details)
      this.details = this.details.filter(m => m.refundQuantity !== 0)
      const params = {
        orderId: this.orderId,
        ifAll: 0,
        details: this.details
      }
      // this.httpPost('/order/refund', params).then(ret => {
      //   this.$emit('loadData')
      //   this.dialogStatus = false
      // }).catch(() => {
      //   console.log('取消退款')
      // })
    }
  }
}
</script>

<style lang="scss" scoped>

/deep/.cell {
  text-align: center;
}
.select_store_btn {
  display: block;
  padding-top: 20px;
  text-align: right;
}
</style>
