<template>
  <div>
    <!-- 搜索 -->
    <el-form :inline="true" :model="queryForm" class="form_border">
      <el-form-item label="订单编号">
        <el-input size="small" v-model="queryForm.orderNo" placeholder="订单编号"></el-input>
      </el-form-item>
      <el-form-item label="设备名称">
        <el-input size="small" v-model="queryForm.deviceName" placeholder="设备名称"></el-input>
      </el-form-item>
        <el-form-item v-if="queryForm.isAll" label="选择公司" prop="enterpriseId">
          <el-autocomplete class="inline-input" v-model="enterpriseName" :fetch-suggestions="queryEnterprise" placeholder="请输入公司名称" @blur="mustSel" @select="enterpriseHandleSelect" clearable></el-autocomplete>
        </el-form-item>
      <el-form-item label="下单时间">
        <el-date-picker
          @change="
              value => {
                changeDate(value, 'startDate')
              }
            "
          v-model="queryForm.startDate"
          type="date"
          placeholder="选择开始时间"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="结束时间" prop="endDate">
        <el-date-picker
          @change="
              value => {
                changeDate(value, 'endDate')
              }
            "
          v-model="queryForm.endDate"
          type="date"
          placeholder="选择结束时间"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button class="_bgcolor" type="primary" @click.stop="search">查询</el-button>
      </el-form-item>
    </el-form>

    <div class="no_order" v-if="!tableData.length">暂无订单</div>

    <!-- 订单信息表格 -->
    <order-item :orderList="tableData" @getSearch="search"></order-item>
      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
  </div>
</template>

<script>
  import OrderItem from './OrderItem'
  export default {
    components: { OrderItem },
    props: ['tableData', 'queryForm'],
    data() {
      return {
        queryInfo: this.queryForm,
        enterpriseName:''
      }
    },
    methods: {
      enterpriseHandleSelect(item) {
        console.log(item)
        this.enterpriseName = item.name
        this.queryInfo.enterpriseId = item.id
      },
      //公司联想查询
      queryEnterprise(queryString, cb) {
        this.associateQuery(queryString, cb, '/basic/enterprise/query', 'httpPost')
      },
      //联想查询
      associateQuery(queryString, cb, url, method) {
        var params = { size: 10, page: 1, total: '', state: 1, name: queryString }
        this[method](url, params, null, true).then(res => {
          res.rows.map(i => (i.value = i.name))
          this.restaurants = res.rows
          cb(this.restaurants)
        })
      },
      mustSel() {
        if (!this.enterpriseId) this.enterpriseName = null
      },
      changePage() {
        this.$emit('getPage', this.queryInfo.page)
      },
      search() {
        this.$emit('getSearch', this.queryInfo)
      },
      refund(id) {
        this.$emit('enable', id)
      },
    },
    watch: {},
    mounted(){

    }
  }
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 200px !important;
  }
}
</style>
